
import axios from 'axios'
import Datetime from 'vux/src/components/datetime/index.vue'
import Toast from 'vux/src/components/toast/index.vue'


export default {
props: ['value'],
components: {
Datetime
},
data() {
const userName = window.localStorage.getItem("userName")
return {
userName,
picName: '',
hasBanner: false,
inputCont: {
money: '',
num: '',
brandTitle: '',
brandTxt: '',
timeStart: '',
timeEnd: '',
mFile: ''
}
}
},
computed: {
total: function() {
return((this.inputCont.money * this.inputCont.num * 100) / 100).toFixed(1)
}
},
methods: {
bdzs() {
if(!/^\d+$/.test(this.inputCont.num)) {
this.inputCont.num="";
this.$vux.toast.show({
type: 'cancel',
text: ''
})
}
},
checkMoney(value) {
if(value > 4999) {
this.$vux.toast.show({
type: 'cancel',
text: '4999'
})
} else if(value < 0.1) {
this.$vux.toast.show({
type: 'cancel',
text: '0.1'
})
} else {


var formattedValue = value
.slice(0, value.indexOf('.') === -1 ?
value.length :
value.indexOf('.') + 2
)

if(formattedValue !== value) {
this.money = formattedValue
}

this.$emit('input', Number(formattedValue))
}
},
getName() {
console.log(this.$refs.file.files)
this.picName = this.$refs.file.files[0].name
this.hasBanner = true
},
give() {
if(this.inputCont.timeStart === '') {
this.$vux.toast.show({
type: 'cancel',
text: ''
})
} else if(this.inputCont.timeEnd === '') {
this.$vux.toast.show({
type: 'cancel',
text: ''
})
} else {
let file = this.$refs.file.files[0]
let param = new FormData() 

param.append('user_name', this.userName)
param.append('total_money', this.total)
param.append('total_num', this.inputCont.num)
param.append('mFile', file)
param.append('start_time', this.inputCont.timeStart)
param.append('end_time', this.inputCont.timeEnd)
param.append('prize_msg', this.inputCont.brandTitle)
param.append('coupon_name', this.inputCont.brandTitle)

let config = {
headers: {
'Content-Type': 'multipart/form-data'
}
}
axios
.post('http://121.42.177.97:8080/red/payred/payred.do', param, config)
.then(res => {
console.log(res)
if(res.data.state === 1) {
this.$vux.toast.show({
text: ''
})
this.$router.replace('/message');
} else {
this.$vux.toast.show({
type: 'cancel',
text: res.data.message
})
}
})
.catch(res => {
this.$vux.toast.show({
type: 'warn',
text: ''
})
throw res;
})
}
},
onChange(val) {
console.log('on change', val)
}
}
}



